import React from 'react';
import { Pie } from 'react-chartjs-2';
import { get_color_set } from '../helper';

export default function LineChart(props) {


  const data = (canvas) => {
    return {
      labels: ['Firefox', 'Word', 'Winamp', 'Spotify', 'Excel', 'Calculator'],
      datasets: [{
        label: 'Apps',
        data: [15, 30, 10, 5, 20, 20],
        backgroundColor: get_color_set(6),
        borderColor: 'transparent'
      }]
    }
  }

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    layout: {
      padding: {
        top: 20,
      },
    },
    legend: {
      position: 'bottom'
    },
    tooltips: {
      displayColors: false,
      mode: 'index',
      intersect: false,
      cornerRadius: 0
    },
  }



  return (
    <div className="chart-cont">
      {props.loading && <div className="loading-table">Loading..</div>}
      {!props.loading && <>

        {props.data.length > 0 ?
          <Pie data={data} options={options} />
          :
          <div className="not-found">
            <img src="/assets/images/not_found.svg" />
            <h5 className="font-weight-light">Sorry, no record found in record :(</h5>
          </div>
        }

      </>}
    </div>
  )
}