import React from 'react';
import { Line } from 'react-chartjs-2';
import { format_date } from './index';

export default function LineChart(props) {


  const data = (canvas) => {
    const ctx = canvas.getContext("2d");
    const gradient = ctx.createLinearGradient(0, 0, 0, 400);
    gradient.addColorStop(0, '#f7ca17');
    gradient.addColorStop(1, '#ffffff');
    return {
      labels: ['Jan', 'Feb', 'Mar', 'Apr', 'Mei', 'Jun', 'Jul', 'Agu', 'Sep', 'Okt', 'Nov', 'Dec'],
      datasets: [{
        label: 'Performance',
        data: [12, 11, 3, 5, 6, 8, 7, 8, 9, 10, 8, 9],
        borderColor: '#444e86',
        backgroundColor: gradient,
        borderWidth: 1
      }]
    }
  }

  const options = {
    responsive: true,
    maintainAspectRatio: false,
    legend: {
      display: false
    },
    tooltips: {
      mode: 'index',
      intersect: false,
      displayColors: false,
      cornerRadius: 0
    },
    hover: {
      mode: 'nearest',
      intersect: true
    },
    elements: {
      line: {
        tension: 0
      }
    },
    scales: {
      yAxes: [{
        display: false,
        ticks: {
          beginAtZero: true,
          display: false
        },
        gridLines: {
          zeroLineColor: '#fff',
          color: '#fff',
          drawBorder: false
        }
      }],
      xAxes: [{
        display: false,
        gridLines: {
          zeroLineColor: '#fff',
          color: '#fff',
          drawBorder: false
        }
      }]
    }
  }



  return (
    <div className="chart-cont">
      {props.loading && <div className="loading-table">Loading..</div>}
      {!props.loading && <>

        {props.data.length > 0 ?
          <Line data={data} options={options} />
          :
          <div className="not-found">
            <img src="/assets/images/not_found.svg" />
            <h5 className="font-weight-light">Sorry, no record found in record :(</h5>
          </div>
        }

      </>}
    </div>
  )
}